import React, { Component } from 'react';
import Layout from './components/Layout';
import NavigationBar from './components/NavigationBar';
import CovidDataCards from './components/CovidDataCards';
import CovidDataCharts from './components/CovidDataCharts';
import Footer from './components/Footer';


class App extends Component {

  constructor(props) {
    super(props)
  
    this.state = {
        confirmed: [],
        recovered: [],
        deaths: [],
        historikudatat: [],
        historikuconfirmed: [],
        historikurecovered: [],
        historikudeaths: [],
        historikuactive: [],
        qytetet: [],
        qytetetcases: [],
        loading: true
    }
  }
  
  componentDidMount() {
    fetch("https://glacial-island-71772.herokuapp.com/users")
      .then(res => res.json())
      .then(result => {
        let list = [];
        let list1 = [];
        let list2 = [];
        let list3 = [];
        let list4 = [];
        let list5 = [];
        let list6 = [];
        result.historiku.forEach(element => {
          list.push(element.data);
        });
        result.historiku.forEach(element => {
          list1.push(element.teKonfirmuara);
        });
        result.historiku.forEach(element => {
          list2.push(element.teSheruara);
        });
        result.historiku.forEach(element => {
          list3.push(element.teVdekur);
        });
        result.qytetet.forEach(element => {
          list4.push(element.qyteti);
        });
        result.qytetet.forEach(element => {
          list5.push(element.raste);
        });
        result.historiku.forEach(element => {
          list6.push(element.teKonfirmuara - element.teSheruara - element.teVdekur);
        });
        this.setState({
            confirmed: result.tePergjithshme.teKonfirmuara,
            recovered: result.tePergjithshme.teSheruara,
            deaths: result.tePergjithshme.teVdekur,
            historikudatat: list,
            historikuconfirmed: list1,
            historikurecovered: list2,
            historikudeaths: list3,
            historikuactive: list6,
            qytetet: list4,
            qytetetcases: list5,
            loading: false
        });
      })
  }

  


  render() {
    return (
      <React.Fragment>
        <NavigationBar />
        <Layout>
          

          <div id="covidinfoparent">
            <div id="covidinfocontainer">
            <a name="firstdiv" />
              <div id="covidinfo">
                <h1>Çka është COVID-19?</h1>
                <hr />
                <p>Sëmundja koronavirus (COVID-19) është një sëmundje infektive e shkaktuar nga një koronavirus i zbuluar se fundi.
                <br />
                <br />
                Shumica e njerëzve të infektuar me virusin COVID-19 do të pësojnë sëmundje të butë deri të moderuar të frymëmarrjes
                <br />
                dhe shërohen pa u kërkuar trajtim të veçantë.
                <br />
                <br />
                Njerëzit e moshuar dhe ata me probleme themelore mjekësore si sëmundja kardiovaskulare, diabeti, sëmundja kronike e frymëmarrjes dhe kanceri ka më shumë të ngjarë të zhvillojnë sëmundje serioze.
                <br />
                <br />
                Mënyra më e mirë për të parandaluar dhe ngadalësuar transmetimin është të informoheni mirë për virusin COVID-19, sëmundjen
                <br />
                që ai shkakton dhe si përhapet. Mbroni veten dhe të tjerët nga infeksioni duke larë duart ose duke përdorur një fshij të bazuar në alkool shpesh dhe duke mos prekur fytyrën tuaj.
                <br />
                <br />
                Virusi COVID-19 përhapet kryesisht përmes pikave të pështymës ose shkarkimit nga hunda kur një person i infektuar kollitet ose teshtit, kështu që është e rëndësishme që edhe ju të praktikoni etiketimet e frymëmarrjes (për shembull, duke kollitur në një bërryl fleksibël).</p>
              </div>
            </div>
          </div>
          
          <CovidDataCards confirmed={this.state.confirmed} recovered={this.state.recovered} deaths={this.state.deaths} loading={this.state.loading} />

          <CovidDataCharts historikudatat={this.state.historikudatat} historikuconfirmed={this.state.historikuconfirmed} historikurecovered={this.state.historikurecovered} historikudeaths={this.state.historikudeaths} historikuactive={this.state.historikuactive} confirmed={this.state.confirmed} recovered={this.state.recovered} deaths={this.state.deaths} qytetet={this.state.qytetet} qytetetcases={this.state.qytetetcases} loading={this.state.loading} />


          


        </Layout>
        <Footer />
      </React.Fragment>
    )
  }
}

export default App;
