import React, { Component } from 'react'
import { Line, Doughnut, Radar } from 'react-chartjs-2';

class CovidDataCharts extends Component {
    


    constructor(props) {
        super(props)
    
        this.state = {
            chartData: {
                labels: [],
                datasets: [
                
                ]
            },
            doughnutData: {
                labels: [
                    'Rastet Aktive',
                    'Rastet e sheruara',
                    'Te vdekur'
                ],
                datasets: [{
                    
                }]
            },
            radarData: {
                labels: [],
                datasets: [
                    
                ]
            }
            
        }
    }






    componentDidUpdate(previousProps) {
        if (previousProps !== this.props) {
            this.setState({
                chartData: {
                    labels: this.props.historikudatat,
                    datasets: [
                        {
                            label: 'Rastet e konfirmuara',
                            fill: false,
                            lineTension: 0.1,
                            backgroundColor: '#F7D147',
                            borderColor: '#F7D147',
                            borderCapStyle: 'butt',
                            borderDash: [],
                            borderDashOffset: 0.0,
                            borderJoinStyle: 'miter',
                            pointBorderColor: '#F7D147',
                            pointBackgroundColor: '#fff',
                            pointBorderWidth: 1,
                            pointHoverRadius: 5,
                            pointHoverBackgroundColor: '#F7D147',
                            pointHoverBorderColor: '#F7D147',
                            pointHoverBorderWidth: 2,
                            pointRadius: 1,
                            pointHitRadius: 10,
                            data: this.props.historikuconfirmed
                        },
                        {
                            label: 'Rastet e sheruara',
                            fill: false,
                            lineTension: 0.1,
                            backgroundColor: '#03A6A6',
                            borderColor: '#03A6A6',
                            borderCapStyle: 'butt',
                            borderDash: [],
                            borderDashOffset: 0.0,
                            borderJoinStyle: 'miter',
                            pointBorderColor: '#03A6A6',
                            pointBackgroundColor: '#fff',
                            pointBorderWidth: 1,
                            pointHoverRadius: 5,
                            pointHoverBackgroundColor: '#03A6A6',
                            pointHoverBorderColor: '#03A6A6',
                            pointHoverBorderWidth: 2,
                            pointRadius: 1,
                            pointHitRadius: 10,
                            data: this.props.historikurecovered
                        },
                        {
                            label: 'Te vdekur',
                            fill: false,
                            lineTension: 0.1,
                            backgroundColor: '#FE736C',
                            borderColor: '#FE736C',
                            borderCapStyle: 'butt',
                            borderDash: [],
                            borderDashOffset: 0.0,
                            borderJoinStyle: 'miter',
                            pointBorderColor: '#FE736C',
                            pointBackgroundColor: '#fff',
                            pointBorderWidth: 1,
                            pointHoverRadius: 5,
                            pointHoverBackgroundColor: '#FE736C',
                            pointHoverBorderColor: '#FE736C',
                            pointHoverBorderWidth: 2,
                            pointRadius: 1,
                            pointHitRadius: 10,
                            data: this.props.historikudeaths
                        },
                        {
                            label: 'Rastet aktive',
                            fill: false,
                            lineTension: 0.1,
                            backgroundColor: '#FE736C',
                            borderColor: '#FE736C',
                            borderCapStyle: 'butt',
                            borderDash: [],
                            borderDashOffset: 0.0,
                            borderJoinStyle: 'miter',
                            pointBorderColor: '#FE736C',
                            pointBackgroundColor: '#fff',
                            pointBorderWidth: 1,
                            pointHoverRadius: 5,
                            pointHoverBackgroundColor: '#FE736C',
                            pointHoverBorderColor: '#FE736C',
                            pointHoverBorderWidth: 2,
                            pointRadius: 1,
                            pointHitRadius: 10,
                            data: this.props.historikuactive
                        }
                    ]
                },
                doughnutData: {
                    datasets: [{
                        data: [(this.props.confirmed - this.props.recovered - this.props.deaths), this.props.recovered, this.props.deaths],
                        backgroundColor: [
                        '#F7D147',
                        '#03A6A6',
                        '#FE736C'
                        ],
                        hoverBackgroundColor: [
                        '#F7D147',
                        '#03A6A6',
                        '#FE736C'
                        ]
                    }]
                },
                radarData: {
                    labels: this.props.qytetet,
                    datasets: [
                        {
                            label: 'Rastet e konfirmuara ne qytete',
                            backgroundColor: 'rgba(179,181,198,0.2)',
                            borderColor: 'rgba(179,181,198,1)',
                            pointBackgroundColor: 'rgba(179,181,198,1)',
                            pointBorderColor: '#fff',
                            pointHoverBackgroundColor: '#fff',
                            pointHoverBorderColor: 'rgba(179,181,198,1)',
                            data: this.props.qytetetcases
                        }
                    ]
                }
            })
        }
    }

    load() 
    {
            return (
                <React.Fragment>
                    <Line
                        data={this.state.chartData}
                    />
                    <Doughnut
                        data={this.state.doughnutData}
                    />
                    <Radar 
                        data={this.state.radarData}
                    />
                </React.Fragment>
            )

    }



    
    render() {
        return (
            <div id="chartsDiv">
                { this.props.loading ? '' : this.load() }


            </div>
        )
    }
}

export default CovidDataCharts
